import { axiosInstance } from '@/main';
import _ from 'lodash';
import router from '@/router';
function getErrorResult(error) {
    console.error(error.config);
    if (error.response) {
        // The request was made and the server responded with a status code
        // that falls out of the range of 2xx
        console.error(error);
        return {
            status: error.response.status,
            errorMessage: error.response.data.errorMessage,
        };
    }
    else if (error.request) {
        // The request was made but no response was received
        // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
        // http.ClientRequest in node.js
        console.error(error.request);
        return {
            status: 400,
            errorMessage: `The request was made but no response was received!`,
        };
    }
    else {
        // Something happened in setting up the request that triggered an Error
        console.error(`Error`, error.message);
        return {
            status: 400,
            errorMessage: error.message,
        };
    }
}
export async function getDataApi(url, id = 0) {
    try {
        const response = await axiosInstance.get(`${url}${id}/`);
        await logoutCheck(response.data);
        return {
            status: response.status,
            code: response.data.responseCode,
            message: response.data.responseMessage,
            data: response.data.responseData,
        };
    }
    catch (error) {
        return getErrorResult(error);
    }
}
export async function createDataApi(url, data, vueForAutoToast = undefined) {
    try {
        const response = await axiosInstance.post(`${url}`, data);
        await logoutCheck(response.data);
        // response.status === 201
        vueForAutoToast && toastResponseMessage(vueForAutoToast, response.data);
        return {
            status: response.status,
            code: response.data.responseCode,
            message: response.data.responseMessage,
            data: response.data.responseData,
        };
    }
    catch (error) {
        return getErrorResult(error);
    }
}
export async function postDataApi(url, data, vueForAutoToast = undefined) {
    return createDataApi(url, data, vueForAutoToast);
}
