export async function getCodeListDataApi(key) {
    try {
        const response = await axiosInstance.get(`/sample/admin/codedet/${key}`);
        await logoutCheck(response.data);
        return response.data.responseData || [];
    }
    catch (error) {
        console.warn(getErrorResult(error).message);
        return [];
    }
}
function toastResponseMessage(vue, responseData) {
    if (_.startsWith(responseData.responseCode, `S`)) {
        vue.$toast.success(responseData.responseMessage);
    }
    else {
        vue.$toast.warning(responseData.responseMessage);
    }
}
async function logoutCheck(responseData) {
    if (responseData.responseCode === 'F004') {
        await router.push(`/login?need=login`);
        return false;
    }
}
